.salon-details {
    padding: 20px;
    text-align: center;
}

.salon-details img {
    width: 80%;
    max-width: 600px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.salon-details h2 {
    font-family: 'Italiana', serif;
    margin-bottom: 20px;
    font-size: 36px;
}

.salon-details h3 {
    margin-top: 20px;
    font-size: 24px;
    font-family: 'Italiana', serif;
}

.salon-details p,
.salon-details li {
    font-size: 18px;
    line-height: 1.6;
}