/* ページ全体のスタイル */
.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('../images/login-background.jpg'); /* 背景画像 */
    background-size: cover;
    background-position: center;
    padding: 30px; /* 画面端との余白 */
    box-sizing: border-box;
}

/* ログインフォーム */
.login-form {
    background-color: white;
    padding: 40px; /* 全体の余白を広げる */
    border-radius: 10px;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.25);
    text-align: center;
    width: 320px; /* フォーム幅を拡大 */
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px; /* 各要素間の間隔を広げる */
}

/* フォームタイトル */
.login-form h2 {
    font-family: 'Italiana', serif;
    margin-bottom: 20px;
    font-size: 28px; /* タイトルを大きく */
}

/* ラベルと入力フィールド */
.login-form label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    font-size: 16px; /* ラベルのフォントを大きく */
    color: #333;
    width: 100%;
}

/* 入力フィールド */
.login-form input {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px; /* 入力フィールド内の余白を拡大 */
    font-size: 16px; /* フォントサイズを拡大 */
    width: 100%;
    box-sizing: border-box;
    outline: none;
}

/* ボタン */
.login-button {
    width: 100%;
    padding: 12px; /* ボタンの高さを調整 */
    background-color: #333;
    color: white;
    font-size: 18px; /* ボタンのフォントサイズを大きく */
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-button:hover {
    background-color: #555;
}

/* 登録リンク */
.register-link {
    margin-top: 20px;
    font-size: 16px; /* リンクのフォントサイズを拡大 */
    color: #555;
    text-align: center;
}

.register-link a {
    color: #007bff;
    text-decoration: none;
}

.register-link a:hover {
    text-decoration: underline;
}