/* Import Google Font Italiana */
@import url('https://fonts.googleapis.com/css2?family=Italiana&display=swap');

/* Header Container */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

/* Logo Styling */
.logo h1 {
  font-family: 'Italiana', serif;
  font-size: 28px;
  margin: 0;
  color: black;
}

/* Navigation Links */
.nav-links {
  display: flex;
  gap: 30px;
}

.nav-links a {
  font-family: 'Italiana', serif;
  font-size: 18px;
  text-decoration: none;
  color: black;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #888;
}

/* Auth Links (Login / Sign up) */
.auth-links {
  font-family: 'Italiana', serif;
  font-size: 16px;
}

.auth-links a {
  text-decoration: none;
  color: black;
  margin-left: 10px;
  transition: color 0.3s ease;
}

.auth-links a:hover {
  color: #888;
}