.hair-salon {
    text-align: center;
    margin: 20px;
}

.hair-salon h2 {
    font-family: 'Italiana', serif;
    margin-bottom: 30px;
}

.salon-grid {
    display: grid !important; /* グリッドレイアウトを必ず適用 */
    grid-template-columns: repeat(3, 1fr) !important; /* 横3列 */
    gap: 20px !important; /* カードの間隔 */
    justify-content: center !important;
    padding: 20px !important;
}

.salon-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 10px;
    overflow: hidden;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 300px;
    height: auto;
}

.salon-card img {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
}

.salon-card p {
    font-family: 'Italiana', serif;
    padding: 10px;
    font-size: 18px;
}